<template lang="pug">
.page-container
    .page-title 值日端-修改個案
    el-form(:model="form" :rules="rules" ref="form" label-width="100px")
        el-row
            el-col(:sm="24" :md="12")
                el-form-item(label="姓名" prop="name")
                    el-input.input-container(placeholder="請輸入姓名"  v-model="form.name")
                el-form-item(label="學號" prop="caseId")
                    el-input.input-container(placeholder="請輸入學號"  v-model="form.caseId")
                el-form-item(label="可諮商時段" prop="time_picker")
                    assignTimePicker(v-model='form.time_picker' ref="timePicker")
            el-col(:sm="24" :md="12")
                el-form-item(label="危機程度" prop="crisis_level")
                    el-select.input-container(placeholder="請輸入危機程度"  v-model="form.crisis_level")
                        el-option(v-for="item in crisis_options" :key="item" :label="item" :value="item")
                el-form-item(label="初談老師" prop="init_counselor_name")
                    el-autocomplete.input-container(v-model='form.init_counselor_name' :fetch-suggestions='querySearch' placeholder='請輸入初談老師')
                el-form-item(label="狀況說明" prop="discription")
                    el-input.input-container(type="textarea" placeholder="請輸入狀況說明"  :autosize="{ minRows: 3, maxRows: 6}" v-model="form.discription")
                el-form-item(label="初談時間" prop="init_consultation_date")
                    el-date-picker(v-model="form.init_consultation_date" type="date")
                el-row
                    el-col(:span="4" :offset="12")
                        el-button(type="danger" @click="cancelForm") 取消
                    el-col(:span="4")
                        el-button(type="primary" @click="submitForm('form')") 儲存
    el-dialog(title="狀態" v-model="dialogVisible" width="30%" @close="handleClose" center)
        el-result(icon="success" title="儲存成功" subTitle="點擊確定關閉提示")
        .dialog-footer(slot="footer" style="text-align: center")
            el-button(type="primary" @click="dialogVisible = false") 確定
</template>

<script>
import assignTimePicker from '@/components/assignTimePicker.vue'
import {getCounselors} from "@/components/getCounselors"
import {weektimeMixin} from "@/components/weektimeMixin"
export default {
    mixins: [getCounselors, weektimeMixin],
    name: 'modifyCase',
    components: {assignTimePicker},
    data () {
        return {
            form: {
                caseId: '',
                name: '',
                time_picker: {
                    available_time: '',
                    semester_label: ''
                },
                crisis_level: '',
                init_counselor_name: '',
                discription: '',
                init_consultation_date: ''
            },
            crisis_options: ['A+','A', 'A-', 'B+', 'B', 'B-', 'C'],
            rules: {
                name: [
                    { required: true, message: '請輸入姓名', trigger: 'blur' }
                ],
                id: [
                    { required: true, message: '請輸入學號', trigger: 'blur' }
                ],
                crisis_level: [
                    { required: true, message: '請選擇危機程度', trigger: 'blur' }
                ],
                init_consultation_date: [
                    { required: true, message: '請選擇日期', trigger: 'blur' }
                ],
                init_counselor_name: [
                    { required: true, message: '請輸入初談老師', trigger: 'change' }
                ]
            },
            dialogVisible: false
        }
    },
    created() {
        this.getCase()
    },
    methods: {
        getCase() {
            this.$http.get("/api/cases/"+this.$route.params.id)
            .then(response => {
                // console.log(response.data.data[0])
                this.form = response.data.data[0]
                this.form.init_consultation_date = this.convertUTCtoStr(this.form.init_consultation_date)
                this.form.time_picker = {
                    available_time: this.form.available_time,
                    semester_label: this.form.semester_label
                }
            })
            .catch((error) => {
                //發送請求失敗時
                console.log(error)
            });
        },
        putCase(id) {
            console.log(this.form.init_consultation_date)
            try{
                this.form.init_consultation_date = this.convertUTCtoStr(this.form.init_consultation_date)
            }
            catch (error) { 
                console.log(error)
            }
            this.$http.put("/api/cases/"+id,{
                caseId: this.form.caseId,
                name: this.form.name,
                semester_label: this.form.time_picker.semester_label,
                available_time: this.form.time_picker.available_time,
                crisis_level: this.form.crisis_level,
                init_counselor_name: this.form.init_counselor_name,
                discription: this.form.discription==''?'無':this.form.discription,
                init_consultation_date: this.form.init_consultation_date
            })
            .then(response => {
                console.log(response.data.message)
                this.dialogVisible = true
                this.$store.dispatch('fetchUnassignCases')
                this.$store.commit('SET_STATUS', 'end')
            })
            .catch((error) => {
                //發送請求失敗時
                console.log(error)
            });
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$store.commit('SET_STATUS', 'pending')
                    this.putCase(this.$route.params.id)
                } else {
                    console.log('error submit!!')
                    return false
                }
            })
        },
        resetForm(formName) {
            this.$refs[formName].resetFields()
            this.$refs.timePicker.clearBlock()
        },
        handleClose() {
            console.log('finish modify')
        },
        cancelForm() {
            this.$router.push('/listCases')
        }
    }
}
</script>
<style lang="sass" scoped>
@import "@/style/common.sass"
</style>
